import { useState } from 'react';
import { ethers } from 'ethers';
import { useWeb3 } from '../contexts/Web3Context';
import { TOKEN_ADDRESS } from '../config/contracts';

const ERC20_ABI = [
  {
    "constant": true,
    "inputs": [],
    "name": "name",
    "outputs": [{"name": "", "type": "string"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "constant": false,
    "inputs": [{"name": "_spender", "type": "address"}, {"name": "_value", "type": "uint256"}],
    "name": "approve",
    "outputs": [{"name": "", "type": "bool"}],
    "payable": false,
    "stateMutability": "nonpayable",
    "type": "function"
  },
  {
    "constant": true,
    "inputs": [],
    "name": "totalSupply",
    "outputs": [{"name": "", "type": "uint256"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "constant": false,
    "inputs": [{"name": "_from", "type": "address"}, {"name": "_to", "type": "address"}, {"name": "_value", "type": "uint256"}],
    "name": "transferFrom",
    "outputs": [{"name": "", "type": "bool"}],
    "payable": false,
    "stateMutability": "nonpayable",
    "type": "function"
  },
  {
    "constant": true,
    "inputs": [],
    "name": "decimals",
    "outputs": [{"name": "", "type": "uint8"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "constant": true,
    "inputs": [{"name": "_owner", "type": "address"}],
    "name": "balanceOf",
    "outputs": [{"name": "balance", "type": "uint256"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "constant": true,
    "inputs": [],
    "name": "symbol",
    "outputs": [{"name": "", "type": "string"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "constant": false,
    "inputs": [{"name": "_to", "type": "address"}, {"name": "_value", "type": "uint256"}],
    "name": "transfer",
    "outputs": [{"name": "", "type": "bool"}],
    "payable": false,
    "stateMutability": "nonpayable",
    "type": "function"
  },
  {
    "constant": true,
    "inputs": [{"name": "_owner", "type": "address"}, {"name": "_spender", "type": "address"}],
    "name": "allowance",
    "outputs": [{"name": "", "type": "uint256"}],
    "payable": false,
    "stateMutability": "view",
    "type": "function"
  },
  {
    "anonymous": false,
    "inputs": [{"indexed": true, "name": "owner", "type": "address"}, {"indexed": true, "name": "spender", "type": "address"}, {"indexed": false, "name": "value", "type": "uint256"}],
    "name": "Approval",
    "type": "event"
  },
  {
    "anonymous": false,
    "inputs": [{"indexed": true, "name": "from", "type": "address"}, {"indexed": true, "name": "to", "type": "address"}, {"indexed": false, "name": "value", "type": "uint256"}],
    "name": "Transfer",
    "type": "event"
  }
];

export const useToken = () => {
  const { provider, account } = useWeb3();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const getBalance = async (address: string): Promise<string> => {
    if (!provider || !address) {
      console.error('Provider ');
      return '0';
    }

    if (!TOKEN_ADDRESS) {
      console.error('');
      return '0';
    }

    try {
      console.log('...', {
        tokenAddress: TOKEN_ADDRESS,
        address,
        provider: provider.constructor.name,
        network: await provider.getNetwork()
      });

      // 
      const code = await provider.getCode(TOKEN_ADDRESS);
      console.log(':', code);
      
      if (code === '0x') {
        console.error('');
        return '0';
      }

      //  provider  signer
      const tokenContract = new ethers.Contract(TOKEN_ADDRESS, ERC20_ABI, provider);
      console.log(':', {
        address: tokenContract.address,
        interface: tokenContract.interface.format()
      });
      
      //  balanceOf 
      try {
        console.log(' balanceOf...');
        //  callStatic 
        const balance = await tokenContract.callStatic.balanceOf(address);
        console.log(':', balance.toString());
        const formattedBalance = ethers.utils.formatEther(balance);
        console.log(':', formattedBalance);
        return formattedBalance;
      } catch (err: any) {
        console.error('balanceOf :', err);
        if (err.code === 'CALL_EXCEPTION') {
          console.error(' balanceOf ');
          console.error(':', {
            code: err.code,
            message: err.message,
            data: err.data,
            transaction: err.transaction
          });
        }
        return '0';
      }
    } catch (error) {
      console.error(':', error);
      return '0';
    }
  };

  const approve = async (spender: string, amount: string): Promise<boolean> => {
    if (!provider || !account) {
      console.error('Provider ');
      return false;
    }

    if (!TOKEN_ADDRESS) {
      console.error('');
      return false;
    }

    try {
      console.log('...', {
        tokenAddress: TOKEN_ADDRESS,
        spender,
        amount,
        account,
        network: await provider.getNetwork()
      });

      // 
      const code = await provider.getCode(TOKEN_ADDRESS);
      console.log(':', code);
      
      if (code === '0x') {
        console.error('');
        return false;
      }

      const tokenContract = new ethers.Contract(TOKEN_ADDRESS, ERC20_ABI, provider.getSigner());
      console.log(':', {
        address: tokenContract.address,
        interface: tokenContract.interface.format()
      });
      
      const amountWei = ethers.utils.parseEther(amount);
      console.log(':', amountWei.toString());
      
      try {
        console.log(' approve...');
        const tx = await tokenContract.approve(spender, amountWei);
        console.log(':', tx.hash);
        await tx.wait();
        console.log('');
        return true;
      } catch (err: any) {
        console.error('approve :', err);
        if (err.code === 'CALL_EXCEPTION') {
          console.error(' approve ');
          console.error(':', {
            code: err.code,
            message: err.message,
            data: err.data,
            transaction: err.transaction
          });
        }
        return false;
      }
    } catch (error) {
      console.error(':', error);
      return false;
    }
  };

  const getAllowance = async (owner: string, spender: string): Promise<string> => {
    if (!provider || !owner || !spender) {
      console.error('Providerowner  spender ');
      return '0';
    }

    if (!TOKEN_ADDRESS) {
      console.error('');
      return '0';
    }

    try {
      console.log('...', {
        tokenAddress: TOKEN_ADDRESS,
        owner,
        spender,
        network: await provider.getNetwork()
      });

      // 
      const code = await provider.getCode(TOKEN_ADDRESS);
      console.log(':', code);
      
      if (code === '0x') {
        console.error('');
        return '0';
      }

      const tokenContract = new ethers.Contract(TOKEN_ADDRESS, ERC20_ABI, provider);
      console.log(':', {
        address: tokenContract.address,
        interface: tokenContract.interface.format()
      });
      
      try {
        console.log(' allowance...');
        //  callStatic 
        const allowance = await tokenContract.callStatic.allowance(owner, spender);
        console.log(':', allowance.toString());
        const formattedAllowance = ethers.utils.formatEther(allowance);
        console.log(':', formattedAllowance);
        return formattedAllowance;
      } catch (err: any) {
        console.error('allowance :', err);
        if (err.code === 'CALL_EXCEPTION') {
          console.error(' allowance ');
          console.error(':', {
            code: err.code,
            message: err.message,
            data: err.data,
            transaction: err.transaction
          });
        }
        return '0';
      }
    } catch (error) {
      console.error(':', error);
      return '0';
    }
  };

  return {
    getBalance,
    approve,
    getAllowance,
    loading,
    error
  };
}; 